.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.sidebar {
  box-shadow: 5px 0 10px rgba(0, 0, 0, 0.1);
  padding: 40px;
}

.input_radius {
  width: 100px;
  height: 50px;
  border: 3px solid #C4C4C4;
  border-radius: 10px;
  padding: 0px 20px 0px 30px;
  font-size: 14px;
  font-family: 'Montserrat';
  color: #373737;
  margin-right: 14px;
}

.text_radius {
  font-size: 20px;
  font-family: 'Montserrat';
  font-weight: 600;
  color: #373737;
}

.text_km {
  font-size: 14px;
  font-family: 'Montserrat';
  font-weight: 600;
  color: #373737;
}

.app {
  display: flex;
}

.sidebar-1 {
  height: 100vh;
  background-color: #fff;
  padding: 25px;
  z-index: 2;
  text-align: center;
}

.sidebar-2 {
  width: 200px;
  background-color: #fff;
  padding: 25px;
  position: relative;
  height: 100vh;
  transition: left 0.4s ease;
  left: -270px; 
  z-index: 1;
  box-shadow: 5px 0 10px rgba(0, 0, 0, 0.1);
  border-left: 3px solid #e8e8e8;
}

.sidebar-2.open {
  left: 0; /* Открывается при нажатии на кнопку */}

.logo img {
  width: 32px;
  height: auto;
  margin-bottom: 40px;
}

.icon_cont {
  width: 60px;
  height: 60px;
  border-radius: 6px;
  border: 3px solid #C4C4C4;
  background-color: #fff;
  cursor: pointer;
}

.search {
  color: #5E7BC7;
  margin-bottom: 15px;
}

.fav {
  color: #C75E5E;
}

.icons {
  display: flex;
  flex-direction: column;
}